import React, { Component } from 'react'
import axios from "axios"
// import debounce from 'lodash.debounce';
import {withRouter} from "react-router-dom"

import "./List.css"
class List extends Component {
    state={
        page:1,
        limit:21,
        contentlist:[],
        noMoreData:false,
    }
    componentDidMount(){
        this.pageGot=0
        this.getVideoList()
        // this.emitDebounced=debounce(this.getVideoList, 250)
    }
    // componentWillUnmount(){
    //     this.emitDebounced.cancel()
    // }
    getVideoList=()=>{
        const {type, tag}=this.props
        const {page, limit}=this.state
        if(page>this.pageGot){
            // console.log(page,this.pageGot);
            this.pageGot=this.pageGot+1
            axios.get(`/video/list?type=${type}&tag=${tag}&page=${page}&limit=${limit}`).then(res=>{
                let {contentlist, page}=this.state
                if(res.data.length>0){
                    this.setState({contentlist:[...contentlist,...res.data],page:page+1})
                }else{
                    this.setState({noMoreData:true})
                }
            })
        }
        
    }
    listScroll=(event)=>{
        let { scrollTop, offsetHeight, scrollHeight }=event.target//this.listWraperNode//
        let bottomHeight=scrollHeight-offsetHeight-scrollTop
        if(!this.state.noMoreData && bottomHeight<500){
            // this.emitDebounced()
            this.getVideoList()
        }
    }
    intoPlayer=(content)=>{
        const {_id:id, bundleName, bundleSize, type}=content
        // const {type}=this.props
        this.props.history.push("/playpage",{id,bundleName,type,bundleSize})
    }
    render() {
        const {contentlist}=this.state
        return (
            <div className="listWraper" onScroll={this.listScroll} ref={listWraperNode=>this.listWraperNode=listWraperNode}>
                <ul className="list" >
                    {
                        contentlist.map(content=>{
                            return(
                                <li className="list_item" onClick={()=>{this.intoPlayer(content)}} key={content._id}>
                                    <img className="coverImg" src={`/imgs/${content.type}/${content.bundleName}.webp`} alt="" />
                                    <p className="ellipsis videoName">{content.bundleName}</p>
                                </li>
                            )
                        })
                        
                    }

                </ul>
            </div>
        )
    }
}

export default withRouter(List)
