import React, { Component } from 'react'
import { Swiper, SwiperSlide } from 'swiper/react';
import SwiperCore, { Thumbs , Navigation} from 'swiper/core';
import 'swiper/swiper.scss';
// import 'swiper/components/pagination/pagination.scss';, Pagination
import 'swiper/components/navigation/navigation.scss';
import List from '../../components/List/List';
import "./Video.css"
SwiperCore.use([Thumbs, Navigation]);


export default class Video extends Component {
    state={
        activeIndex:0,//哪个tag需要被高亮
        thumbsSwiper:null,
    }
    // clickTab=(index)=>{
    //     this.mainSwiper.slideTo(index)
    // }
    render() {
        const {activeIndex, thumbsSwiper}=this.state
        const {type ,tags, tagsCN}=this.props
        return (
            <div className="videoWraper">
                {/* <div className="tagNav">
                    {
                        tags.map((tag, index) => {
                            return (
                                <button
                                key={index} 
                                onClick={() => { this.clickTab(index) }} 
                                className={index === activeIndex ? "tag tagOn" : "tag"} 
                                >
                                    {tagsCN[tag]}
                                </button>)
                        })
                    }
                </div> */}
                <Swiper 
                className="tagNav"
                onSwiper={thumbsSwiper=>{this.setState({thumbsSwiper})}}
                watchSlidesVisibility={true}
                watchSlidesProgress={true}
                spaceBetween={10}
                slidesPerView={4.8}
                // navigation
                >
                    {
                        tags.map((tag,index)=>{
                            return (<SwiperSlide 
                                key={index} 
                                className={index === activeIndex ? "tag tagOn" : "tag"} 
                                >{tagsCN[tag]}</SwiperSlide>)
                        })
                    }
                </Swiper>

                <Swiper
                    className="videoSwiper"
                    spaceBetween={10}
                    slidesPerView={1}
                    onSlideChange={(swiper) => this.setState({ activeIndex: swiper.activeIndex })}
                    // onSwiper={mainSwiper => { this.mainSwiper=mainSwiper }}
                    thumbs={{ swiper: thumbsSwiper }}
                >
                    {
                        tags.map((tag, index) => {
                            return (<SwiperSlide key={index} style={{ overflow: "auto" }} className="list_slide">
                                <List tag={tag} type={type} />
                            </SwiperSlide>)
                        })
                    }
                </Swiper>


            </div>
        )
    }
}
