import React, { Component } from 'react'
import {withRouter} from "react-router-dom"
import "./SearchList.css"

class SearchList extends Component {
    intoPlayer=(content)=>{
        const {_id:id, bundleSize, type}=content
        // const {type}=this.props
        this.props.history.push("/playpage",{id,type,bundleSize})
    }
    render() {
        const {contentlist}=this.props
        // console.log(contentlist);
        let typesCN={
            "Movies":"电影",
            "Animations":"动漫",
            "Tvs":"电视剧",
        }
        return (
            // <div className="search_listWraper" >
                <ul className="search_list" >
                    {
                        contentlist.map(content=>{
                            return(
                                <li className="search_list_item" onClick={()=>{this.intoPlayer(content)}} key={content._id}>
                                    <span className="search_result_name">
                                        {content.bundleName}
                                    </span>
                                    <span className="search_result_type">
                                        {typesCN[content.type]}
                                    </span>
                                </li>
                            )
                        })
                        
                    }

                </ul>
            // </div>
        )
    }
}

export default withRouter(SearchList)
