/**
 * EpisodeList.js
 *
 * This is just a plain ol' React component.
 * the vjsComponent methods, player methods etc. are available via
 * the vjsComponent prop (`this.props.vjsComponent`)
 */
import React, { Component,  } from 'react';//PropTypes
import ReactDOM from 'react-dom';
import videojs from 'video.js';
import pubsub from "pubsub-js"
import "./EpisodeList.css"

class EpisodeList extends Component {
    state={
        isShow:false,
        e:1,
    }
    componentDidMount(){
        pubsub.subscribe("sete_inline",this.sete_inline)
        pubsub.subscribe("toggleList",this.toggleList)
    }
    componentWillUnmount(){
        pubsub.unsubscribe("sete_inline")
        pubsub.unsubscribe("toggleList")
    }
    sete_inline=(_,e)=>{
        this.setState({e})
    }
    startep_local=(i)=>{
        const { startep, s } = this.props.body
        startep(null,{s,e:i+1})
        this.setState({isShow:false, e:i+1})
    }
    toggleList=(_,newisShow)=>{
        if(newisShow==="auto"){
            this.setState({isShow:!this.state.isShow})
        }else{
            this.setState({isShow:newisShow})
        }
    }
    render() {
        const { bundleSize, s } = this.props.body
        let chapters = []
        for (let i = 0; i < bundleSize["season_" + s]; i++) {
            chapters.push(
                <li className={this.state.e===(i+1) ? "epLi epLi_active" : "epLi"} onClick={()=>this.startep_local(i)} key={i}>{i<9 ? "0"+(i+1) : (i+1)}</li>
            )
        }

        return (
            <div className="epUlWarper" style={{display: this.state.isShow ? "block" : "none"}}>
                <ul className="epUl">{chapters}</ul>
            </div>
        );
    }
}


/**
 * vjsEpisodeList.js
 * Here is where we register a Video JS Component and
 * mount the React component to it when the player is ready.
 */
//  import EpisodeList from './EpisodeList';


const vjsComponent = videojs.getComponent('Component');

class vjsEpisodeList extends vjsComponent {

    constructor(player, options) {
        super(player, options);
        this.data = options
        /* Bind the current class context to the mount method */
        this.mount = this.mount.bind(this);

        /* When player is ready, call method to mount React component */
        player.ready(() => {
            this.mount();
        });

        /* Remove React root when component is destroyed */
        this.on("dispose", () => {
            ReactDOM.unmountComponentAtNode(this.el())
        });
    }

    /**
     * We will render out the React EpisodeList component into the DOM element
     * generated automatically by the VideoJS createEl() method.
     *
     * We fetch that generated element using `this.el()`, a method provided by the
     * vjsComponent class that this class is extending.
     */
    mount() {
        ReactDOM.render(<EpisodeList vjsComponent={this} body={this.data} />, this.el());
    }
}

/**
 * Make sure to register the vjsComponent so Video JS knows it exists
 */
vjsComponent.registerComponent('vjsEpisodeList', vjsEpisodeList);

export default vjsEpisodeList;
