import React, { Component } from 'react'
import {withRouter} from "react-router-dom"
import "./SearchWraper.css"

class SearchWraper extends Component {
    // state={
    //     fakeOn:true,
    // }

    // toggleFake=(event)=>{
    //     event.preventDefault()
    //     const {fakeOn}=this.state
    //     this.setState({fakeOn:!fakeOn})
    // }
    toSearch=(event)=>{
        event.preventDefault()
        this.props.history.push("/search")
    }
    render() {
        return (
            <div className="search_fakebox">
                <div className="search_fake">
                    <div className="search_fake_outer" onClick={this.toSearch}>
                        <input className="search_fake_input" placeholder="输入内容进行搜索"  />
                        <span className="search_fake_icon"></span>
                    </div>
                </div>
            </div>
        )
    }
}

export default withRouter(SearchWraper)