import React, { Component } from 'react'
import videojs from 'video.js'
import 'video.js/dist/video-js.css'
import "./VideoPlayer.css"
import "videojs-playlist/dist/videojs-playlist.es"
import pubsub from "pubsub-js"
// eslint-disable-next-line
import vjsEpisodeList from "./EpisodeList/EpisodeList"
// eslint-disable-next-line
import vjsEpisodeButton from "./EpisodeButton/EpisodeButton"
// eslint-disable-next-line
import vjsNextEpisode from "./NextEpisode/NextEpisode"
import VjsMsg from "./VjsMsg/VjsMsg"

export default class VideoPlayer extends Component {
    state = {
        s: 1,
    }
    create_allplaylist = () => {
        let allplaylist = []
        let { bundleSize, id, type } = this.props.videoInfo
        let bundleKeys = Object.keys(bundleSize)
        bundleKeys.forEach((key, index) => {
            let list = []
            for (let i = 0; i < bundleSize[key] * 1; i++) {
                let item = {
                    sources: [{
                        src: `/video/getvideo?id=${id}&type=${type}&s=${index + 1}&e=${i + 1}`,
                        type: 'video/mp4',
                    }]
                }
                list.push(item)
            }
            allplaylist.push(list)
        });
        return allplaylist
    }
    startep = (_, data) => {
        const { s } = this.state
        if (data.s !== s) {
            this.setState({ s: data.s })
        }
        if (this.player && this.player.playlist) {
            if(data.e!==(this.player.playlist.currentItem()+1) || data.s!==s){
                this.player.playlist.currentItem(data.e - 1)
            }
        }
    }
    nextep=()=>{
        let s=this.state.s
        let { bundleSize } = this.props.videoInfo
        let maxe=bundleSize[Object.keys(bundleSize)[s-1]]
        let e=this.player.playlist.currentItem() + 1
        if(e<maxe){
            let data={
                s,
                e:e+1,
            }
            this.startep(null,data)
        }
        
    }
    addSubs=(s,e)=>{
        let { id, type } = this.props.videoInfo
        this.currentsubs=this.player.addRemoteTextTrack({
            src: `/video/getsubs?id=${id}&type=${type}&s=${s}&e=${e}`,
            default:true,
            label:"中文",
            mode: 'showing',
        },true)
    }
    togglePlay=(e)=>{
        if(this.player.paused()){
            this.player.play()
        }else{
            this.player.pause()
        }
    }
    touchend=(event)=>{
        let {startX, player, moveDirection}=this
        if(moveDirection!==0){
            let endPoint=event.changedTouches[0]
            const {clientX:endX}=endPoint
            let Dx=endX-startX
            // let Dy=endY-prevY
            if(moveDirection===1){
                //x方向
                if(Math.abs(Dx)>20){
                    let newtime=player.currentTime()+0.05*Dx
                    newtime=newtime>0 ? newtime : 0
                    player.currentTime(newtime)
                    if(Dx>0){
                        this.vjsNode.vjsMsg(`前进${parseInt(0.05*Dx)}秒`,true)
                    }else{
                        this.vjsNode.vjsMsg(`后退${-parseInt(0.05*Dx)}秒`,true)
                    }
                }else{
                    this.vjsNode.vjsMsg(null,true)
                }
            }else if(moveDirection===2){
                //y方向 
                this.vjsNode.vjsMsg(null,true)
            }
        }else{
            pubsub.publish("toggleList",false)
        }
    }
    touchmove=(event)=>{
        let {startX, lastMoveX, lastMoveY, player, moveDirection}=this
        let movePoint=event.changedTouches[0]
        const {clientX:moveX, clientY:moveY}=movePoint
        let dx=moveX-lastMoveX//>0向右
        let dy=moveY-lastMoveY//>0向上
        let absx=Math.abs(dx)
        let absy=Math.abs(dy)
        let Dx=moveX-startX
        // let Dy=moveY-prevY
        if(moveDirection===0){
            this.moveDirection=(absx>=absy ? 1 : 2)//1表示x方向, 2表示y,0 表示没有方向
        }
        if(moveDirection===1){
            //x方向移动
            if(Dx>=0){
                this.vjsNode.vjsMsg(`前进${parseInt(0.05*Dx)}秒`)
            }else{
                this.vjsNode.vjsMsg(`后退${-parseInt(0.05*Dx)}秒`)
            }
            
        }else if(moveDirection===2){
            //y方向移动
            let newvolume=player.volume()-0.005*dy
            // console.log(player.volume());
            if(newvolume>1){
                newvolume=1
            }else if(newvolume<0){
                newvolume=0
            }
            player.volume(newvolume)
            this.vjsNode.vjsMsg(`音量${parseInt(newvolume*100)}%`,false)
        }
        this.lastMoveX=moveX
        this.lastMoveY=moveY
    }
    touchstart=(event)=>{
        if(this.prevTime){
            if(this.moveDirection===0){
                //没有方向移动
                let currTime=(new Date()).getTime()
                let startPoint=event.changedTouches[0]
                let startX=startPoint.clientX
                let startY=startPoint.clientY
                if(
                currTime-this.prevTime<500 &&
                Math.abs(startX-this.startX)<50 &&
                Math.abs(startY-this.startY)<50
                ){
                    this.togglePlay()
                }
            }
            
        }
        this.prevTime=(new Date()).getTime()
        this.startPoint=event.changedTouches[0]
        this.lastMoveX=this.startX=this.startPoint.clientX
        this.lastMoveY=this.startY=this.startPoint.clientY
        this.moveDirection=0

    }
    componentDidMount() {
        let that = this
        // let { id, type } = this.props.videoInfo
        this.allplaylist = this.create_allplaylist()
        let { s } = this.state
        const {addSubs , startep, nextep}=this
        let videoJsOptions = {
            autoplay: true,
            controls: true,
            fluid: true,
            preLoad:true,

        }
        this.player = videojs(this.videoNode, videoJsOptions, function onPlayerReady() {
            this.playlist(that.allplaylist[s - 1])
            this.playlist.autoadvance(0);
            // addSubs(1,1)
            this.on("firstplay", () => {
                let e = this.playlist.currentItem() + 1
                pubsub.publish("sete", e)
                pubsub.publish("sete_inline", e)
                let { s } = that.state
                addSubs(s,e)
                //设置字幕样式
                var settings = this.textTrackSettings;
                settings.setValues({
                    // "backgroundColor": "rgb(66, 54, 54)",
                    "backgroundOpacity": "0",
                    "edgeStyle": "dropshadow",
                });
                settings.updateDisplay();
            })
            pubsub.subscribe("startep", startep)
            // console.log(this.getChild('controlBar'));
            this.getChild('controlBar').addChild("vjsEpisodeButton", {}, 10);
            this.getChild('controlBar').addChild("vjsNextEpisode", {nextep}, 1);
            this.addChild("vjsEpisodeList", {
                s,
                startep,
                bundleSize: that.props.videoInfo.bundleSize,
            });
        });
    }

    componentWillUnmount() {
        pubsub.unsubscribe("startep")
        if (this.player) {
            this.player.dispose()
        }
        
    }
    render() {
        if (this.player && this.player.playlist) {
            this.player.playlist(this.allplaylist[this.state.s - 1])
        }
        return (
            <div className="vjs-wraper">
                <div data-vjs-player >
                    <VjsMsg ref={node=>this.vjsNode=node} />
                    <video 
                    ref={node => this.videoNode = node} 
                    className="video-js" 
                    onTouchEnd={this.touchend} 
                    onTouchStart={this.touchstart} 
                    onTouchMove={this.touchmove}
                    ></video>
                </div>
            </div>

        )
    }
}


