import React, { Component } from 'react'
import axios from "axios"
import "./Search.css"
import SearchList from "./SearchList/SearchList"


export default class Search extends Component {
    state={
        // contentlist:[],
        MoviesList:[],
        AnimationsList:[],
        TvsList:[]
    }
    pressEnter=(event)=>{
        if(event.code==="Enter" || event.keyCode===13) {
            this.search()
        }
    }
    search=()=>{
        const {value}=this.inputNode;
        // this.setState({contentlist:[]})
        // if(value===""){return}
        this.inputNode.blur()
        let types=["Movies", "Animations", "Tvs"]
        types.forEach(type=>{
            axios.get(`/video/search?keywords=${value}&type=${type}`).then(res=>{
                // console.log(res.data);
                this.setState({[type+"List"]:res.data})
                // this.props.location.state=value
            })
        })
    }
    render() {
        const {MoviesList, AnimationsList, TvsList }=this.state
        // const contentlist=[...MoviesList, ...AnimationsList, ...TvsList]
        // console.log(contentlist);
        return (
            <div className="search_page">
                <div className="search_box">
                    <div className="search_outer">
                        <input autoFocus className="search_input" placeholder="输入内容进行搜索" onKeyUp={this.pressEnter} ref={inputNode=>this.inputNode=inputNode} />
                        <span className="search_icon" onClick={this.search}></span>
                    </div>
                </div>
                <SearchList contentlist={[...MoviesList, ...AnimationsList, ...TvsList]} />
            </div>
        )
    }
}
