import React, { Component } from 'react'
import {Route, NavLink, Redirect, withRouter} from "react-router-dom"
import CacheRoute, { CacheSwitch } from 'react-router-cache-route'

import "./App.css"
import SearchWraper from './components/SearchWraper/SearchWraper'
import Search from './paths/Search/Search'
import Movie from './paths/Movie/Movie'
import Tv from "./paths/Tv/Tv"
import Animation from "./paths/Animation/Animation"
import PlayPage from './paths/PlayPage/PlayPage'


class app extends Component {
    render() {
        let currentPath=this.props.location.pathname
        const noTabPaths=["/playpage", "/search"]
        let hide=noTabPaths.findIndex(path=>path===currentPath)>=0
        return (
            <div className="app">
                <div className="appWraper">
                    { !hide ? <SearchWraper /> : ""}
                    { !hide ?
                    (<div className="nav">
                        <NavLink className="link" activeClassName="linkActive" to="/movie">电影</NavLink>
                        <NavLink className="link" activeClassName="linkActive" to="/animation">动漫</NavLink>
                        <NavLink className="link" activeClassName="linkActive" to="/tv">电视剧</NavLink>
                    </div>) :
                    ""
                    }
                    <CacheSwitch>
                        <Route path="/movie" component={Movie}/>
                        <Route path="/animation" component={Animation}/>
                        <Route path="/tv" component={Tv}/>
                        <Route path="/playpage" component={PlayPage}/>
                        <CacheRoute className="searchCacheRoute" path="/search" component={Search} />
                        <Redirect to="/movie" />
                    </CacheSwitch>
                    
                </div>
                
            </div>
        )

    }
}

export default withRouter(app)